<template>
  <div class="position-relative">
    <b-row class="ml-2 p-3 mb-2">
      <b-col class="col-1  mt-2 ml-3">
        <h6 class="header-pretitle ">
          <img src="/img/mockframe/logo.svg" alt />
        </h6>
      </b-col>
      <b-col class="col-6  mt-3 ml-5 ">
        <div class="d-flex align-items-center" v-if="project && project.name">
          <h2 class="first-letter-caps mb-1" v-if="project && project.name">{{ project.name }}</h2>
          <p class="mb-0 text-muted ml-2" v-if="project && project.description">{{ project.description }}</p>
        </div>
        <div class="d-flex align-items-center" v-else>
          <h2 class="first-letter-caps mb-1">No Project Found</h2>
        </div>
      </b-col>
    </b-row>
    <hr />
    <div>
      <Mocks ref="mockDetails" :project="project" />
    </div>
  </div>
</template>
<script>
  import Mocks from "./Mocks";
  import MockFrame from "@/js/mockframe/mockframe";

  export default {
    components: {
      Mocks,
    },
    data() {
      return {
        domainId: window.domain,
        projectId: window.user,
        project: null,
      };
    },

    async mounted() {
      await this.getProject();
    },
    methods: {
      async getProject() {
        this.project = await MockFrame.getProject(this.domainId, this.projectId, this.$route.query.p);
      },
    },
  };
</script>

